import { useEffect, useState } from 'react'
import './css/App.css'

import WelcomePage from './components/WelcomePage'
import AnswerPage from './components/AnswerPage'
import PublishPage from './components/PublishPage'
import CommentPage from './components/CommentPage'

import Background from './assets/background.jpg'
import Tree from './assets/little-tree.svg'
import RoundTree from './assets/round-tree.png'
import ForkTree from './assets/fork-tree.png'
import Weed from './assets/weed.png'
import WhiteFlower from './assets/white-flower.png'
import PurpleFlower from './assets/purple-flower.png'
import PinkFlower from './assets/pink-flower.png'
import Pine from './assets/pine.png'
import Comet from './assets/comet.png'
import Trigon from './assets/trigon.png'
import Cloud from './assets/cloud.png'

// import interface
import { Item } from './typings/template'

const itemMap = {
    'tree': Tree,
    'round-tree': RoundTree,
    'fork-tree': ForkTree, 
    'weed': Weed,
    'white-flower': WhiteFlower,
    'purple-flower': PurpleFlower, 
    'pink-flower': PinkFlower, 
    'pine': Pine, 
    'comet': Comet,
    'trigon': Trigon,
    'cloud': Cloud,
}

const defaultItem = {
    type: 'tree',
    x: 250,
    y: 850,
    author: 'OrangeX4',
    comment: 'Hello World!'
} as Item


function getGiteeData(callback: (data: Item[], sha: string) => void) {
    let xhr = new XMLHttpRequest()
    xhr.open("GET", "https://gitee.com/api/v5/repos/no-code-no-life/forest/contents/data.json?access_token=530e4f2c2affaae9e74bc97931904f8d&ref=master", true)
    xhr.onreadystatechange = function () {
        if (xhr.readyState === 4 && xhr.status === 200) {
            let responseText = xhr.responseText
            let obj = JSON.parse(responseText)
            callback(JSON.parse(decodeURIComponent(window.atob(obj.content))), obj.sha)
        }
    }
    xhr.send()
}

function App() {
    const clientWidth = document.documentElement.clientWidth
    const clientHeight = document.documentElement.clientHeight

    function updateGiteeData(item: Item) {
        getGiteeData((data, sha) => {
            data = [...data, item]
            let xhr = new XMLHttpRequest()
            xhr.open("PUT", "https://gitee.com/api/v5/repos/no-code-no-life/forest/contents/data.json?access_token=530e4f2c2affaae9e74bc97931904f8d&ref=master", true)
            xhr.setRequestHeader("Content-Type", "application/json;charset=UTF-8")
            xhr.onreadystatechange = function () {
                if (xhr.readyState === 4 && xhr.status === 200) {
                    setData(data)
                }
            }
            console.log(JSON.stringify(data))
            console.log(window.btoa(encodeURIComponent(JSON.stringify(data))))
            xhr.send(JSON.stringify({
                access_token: "530e4f2c2affaae9e74bc97931904f8d",
                content: window.btoa(encodeURIComponent(JSON.stringify(data))),
                sha: sha,
                message: "Update",
                branch: "master"
            }))
        })
    }

    // Cloud data
    const [data, setData] = useState([
        {
            type: 'tree',
            x: 200,
            y: 800,
            author: '初始化中...',
            comment: 'Hello World!'
        }
    ] as Item[])
    useEffect(() => {
        getGiteeData((data) => {
            setData(data)
        })
    }, [])

    const [currentItemIndex, setCurrentItemIndex] = useState(-1)

    // Native data
    const [nativeItem, setNativeItem] = useState(null as Item | null)


    // State Machine
    const [page, setPage] = useState('WELCOME')

    function handleGameClick(e: React.MouseEvent<HTMLDivElement, MouseEvent>) {
        if (nativeItem) {
            setNativeItem({ ...nativeItem, x: e.nativeEvent.offsetX, y: e.nativeEvent.offsetY })
        }
    }

    return (
        <div className="App">
            <div onClick={handleGameClick} className={clientHeight > clientWidth ? "game-top" : "game-left"}>
                {
                    data.map((item, index) =>
                        <img src={itemMap[item.type]}
                            key={index}
                            alt={item.author + '\'s ' + item.type}
                            onClick={(e) => {
                                setCurrentItemIndex(index)
                                setPage('COMMENT')
                                e.stopPropagation()
                            }}
                            style={{
                                marginTop: item.y,
                                marginLeft: item.x,
                                position: 'absolute'
                            }} />
                    )
                }
                {
                    nativeItem ?
                        <img src={itemMap[nativeItem.type]}
                            alt={nativeItem.author + '\'s ' + nativeItem.type}
                            onClick={(e) => { e.stopPropagation() }}
                            style={{
                                marginTop: nativeItem.y,
                                marginLeft: nativeItem.x,
                                position: 'absolute',
                                border: '2px solid #FC0'
                            }} />
                        : null
                }
                <img src={Background} alt='background' />
            </div>

            <div className={clientHeight > clientWidth ? "show-root-bottom" : "show-root-right"}>
                <div className="show-content">
                    {(() => {
                        switch (page) {
                            case 'WELCOME':
                                return (
                                    <WelcomePage data={data}
                                        onAnswer={() => { setPage('ANSWER') }} />
                                )
                            case 'ANSWER':
                                return (
                                    <AnswerPage onSuccess={() => {
                                        setPage('PUBLISH')
                                    }}
                                        onCancel={() => {
                                            setPage('WELCOME')
                                        }} />
                                )
                            case 'PUBLISH':
                                return (
                                    <PublishPage item={nativeItem ? nativeItem : defaultItem}
                                        onChange={(item) => {
                                            setNativeItem(item)
                                        }}
                                        onSuccess={(name, comment) => {
                                            if (nativeItem) {
                                                updateGiteeData({
                                                    ...nativeItem,
                                                    author: name,
                                                    comment
                                                } as Item)
                                                // setData([...data, {
                                                //     ...nativeItem,
                                                //     author: name,
                                                //     comment
                                                // } as Item])
                                            }
                                            setNativeItem(null)
                                            setPage('WELCOME')
                                        }}
                                        onCancel={() => {
                                            setNativeItem(null)
                                            setPage('WELCOME')
                                        }} />

                                )
                            case 'COMMENT':
                                return (
                                    <CommentPage item={data[currentItemIndex]}
                                        onCancel={() => {
                                            setNativeItem(null)
                                            setPage('WELCOME')
                                        }} />

                                )
                            default:
                                return (
                                    <WelcomePage data={data}
                                        onAnswer={() => { setPage('ANSWER') }} />
                                )
                        }
                    })()}
                </div>
            </div>
        </div>
    )
}

export default App
